.benefits {
  list-style-type: none;
  margin: 0 auto;
  padding: 0;

  @media (min-width: $tablet-width) {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
}

.benefits__item {
  margin: 0 auto;
}

.benefits__title {
  display: block;
  margin: 0;
  margin-bottom: 14px;
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  text-align: center;
}

.benefits__title--advice {
  text-align: start;

  @media (min-width: $tablet-width) {
    text-align: center;
  }
}

.benefits__description {
  display: block;
  margin: 0 auto;
  text-align: center;
}

.benefits__description--advice {
  text-align: start;

  @media (min-width: $tablet-width) {
    text-align: center;
  }
}

.benefits__description--number {
  display: block;
  margin-bottom: 17px;
}

.benefits__picture {
  display: block;
  margin: 0;

  @media (min-width: $tablet-width) {
    min-width: 512px;
    height: 256px;
  }

  @media (min-width: $desktop-width) {
    min-width: 800px;
    height: 256px;
  }
}

.benefits__item-wrapper {
  margin: 0;
}

.benefits__item--theme {
  background-color: #81b3d2;
  color: #ffffff;

  @media (min-width: $tablet-width) {
    display: flex;
    flex-direction: row;
  }
}

.benefits__item--advice {
  background-color: #ffffff;
  padding-top: 30px;
  padding-left: 66px;
  padding-right: 18px;

  @media (min-width: $tablet-width) {
    max-width: 256px;
    margin: 0;
    box-sizing: border-box;
    padding-top: 158px;
    padding-bottom: 59px;
    padding-left: 35px;
    padding-right: 35px;
  }

  @media (min-width: $desktop-width) {
    max-width: 400px;
    margin: 0;
  }
}

.benefits__item--view {
  background-color: #eeeeee;
  padding: 25px;
  padding-left: 35px;
  padding-right: 35px;

  @media (min-width: $tablet-width) {
    max-width: 256px;
    margin: 0;
    box-sizing: border-box;
    padding-top: 42px;
    padding-bottom: 63px;
    padding-left: 34px;
    padding-right: 34px;
  }

  @media (min-width: $desktop-width) {
    max-width: 400px;
    margin: 0;
    min-height: 256px;
  }
}

.benefits__item-wrapper--one {
  max-width: 320px;
  margin: 0 auto;
  padding-top: 40px;
  padding-bottom: 40px;
  padding-left: 16px;
  padding-right: 22px;

  @media (min-width: $desktop-width) {
    max-width: 400px;
    margin: 0;
  }
}

.benefits__item-wrapper--two {
  max-width: 320px;
  margin: 0 auto;
  padding-top: 39px;
  padding-bottom: 35px;
  padding-left: 15px;
  padding-right: 16px;
  margin-top: 52px;
  order: 1;

  @media (min-width: $tablet-width) {
    margin-top: 0;
  }

  @media (min-width: $desktop-width) {
    max-width: 400px;
    margin: 0;
  }
}
